* {
  font-family: helvetica, arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: darkblue;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 6px;
  margin-right: 6px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;

}

.card img {
  max-height: 300px;
}